.form-search {
    margin-top: 90px;
    position: relative;
}
.form-image {
    position: absolute;
    top: 50px;
    right: 30px;
    max-width: 985px;
    width: 100%;
}
.filtered-form {
    margin-bottom: 35px;
}
.filtered-form-image-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 160px;
    height: 200px;
    box-shadow: 0 0 42px rgba(176, 170, 170, 0.35);
    background-color: #D6E5F7;
}
.filtered-form-image {
    height: 90%;
}
.filtered-form-description {
    color: #666;
