}
.filtered-form-component {
    margin-bottom: 85px;
}
.filtered-form-component .btn {
    margin-left: 12px;
}
.available-forms {
    margin-top: 200px;
}
.available-forms h2 {
    margin-bottom: 65px;
}
.form-details {
    margin-left: 25px;
    width: 100%;
}
.form-details > .row img {
    height: 16px;
    width: 16px;
}
.form-details > .row p {
    font-size: 1.5rem;
    margin: 0 6px;
}

@media (max-width: 1200px) {
    .form-image {
        opacity: .3;
    }
}

@media (max-width: 992px) {
    .available-forms > .row {
        flex-direction: column-reverse;
    }
}
.home {
    margin-top: 100px;
}
/* FindForm component */
.find-form {
    position: relative;
}
.find-form-image {
    position: absolute;
    top: 10%;
    right: 180px;
    max-width: 820px;
}
.block-find-form {
    display: flex;
    flex-direction: column;
    padding: 192px 0;
}
.docas-find-form-button {
    height: 60px;
    width: 200px;
    margin: 19px auto;
    background-color: #0b51ca;
    border: none;
    font-size: 1.8rem;
    line-height: 55px;
}
.docas-show-more {
    text-align: center;
}
.docas-show-more-button {
    height: 60px;
    width: 200px;
    margin-top: 10px;
    padding: 0 30px;
    background-color: #0b51ca;
    border: none;
    font-size: 2rem;
}
.block-find-form h1 {
    margin-bottom: 58px;
}
/* LastForms component */
.block-last-form {
    margin-top: 177px;
}
/* ListForm component */
.square {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 570px;
    width: 100%;
    height: 44rem;
    margin: 1.4rem;
    background-color: #d9e8f7;
    border-radius: 5px;
}
.square .nav {
    height: 60px;
    margin: 34px 19px 34px 0;
}
.square .badge {
    font-size: 2.2rem;
    height: 45px;
    max-width: 110px;
    padding: 10px 18px;
    margin: 18px 25px;
    font-weight: normal;
}
.square-btns {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: flex-end;
}
.square-btns li {
    padding: 10px 18px;
    margin-left: 6px;
    font-size: 2.1rem;
}
.form-filter {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0 40px;
    margin-top: 50px;
}
.form-filter p {
    margin: 0 0 0 35px;
    line-height: 40px;
    font-weight: bold;
}
.form-filter h3 {
    line-height: 44px;
    margin: 0 0 60px 28px;;
}
.form-btns-filters {
    display: flex;
    justify-content: center;
    margin: 60px 0 0;
}
.form-btns-filters .btn {
    font-size: 2.1rem;
}
.form-btns-filters .btn-link {
    font-size: 2.1rem;
    color: black;
    text-decoration: none;
}
.form-btns-filters .btn-link:hover {
    border-bottom: 2px solid #0b51ca;
    border-radius: 0;
}
/* explore component */
.explore-docas {
    position: relative;
    margin-top: 220px;
    overflow: hidden;
}
.explore-docas > .row > img {
    position: absolute;
    right: -70px;
    top: 35px;
}
.explore-docas-text {
    width: 475px;
    margin: 0 0 0 122px;
}
.explore-docas-text p {
    font-size: 2.3rem;
    margin: 56px 0 0 4px;
    line-height: 37px;
}
.explore-docas-btns {
    margin: 70px 0 0 7px;
}
.explore-docas-btns button {
    height: 61px;
    width: 200px;
    margin-right: 12px;
    font-size: 2rem;
    text-transform: uppercase;
}
.macbook {
    display: flex;
    justify-content: center;
    padding: 32px 0px;
}
/* statistics component */
.statistics {
    position: relative;
}
.statistics img {
    position: absolute;
    top: 243px;
    left: -65px;
}
.statistics-row {
    justify-content: space-between;
}
.statistics-value {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 215px;
    height: 215px;
    border-radius: 108px;
    box-shadow: 0px 22px 30px rgba(69,137,254,0.35);
    background-color: #ffffff;
    padding: 30px 0 0 0;
}
.statistics-value h5, p, span {
    font-weight: bold;
}
.statistics-row .statistics-value:nth-child(1) {
    margin: 255px 0 0;
}
.statistics-row .statistics-value:nth-child(2) {
    margin: 120px 0 0;
}
.statistics-row .statistics-value:nth-child(3) {
    margin: 305px 0 0;
}
.statistics-row .statistics-value:nth-child(4) {
    margin: 195px 0 0;
}
.statistics-value h4 {
    font-weight: bold;
}
.statistics-value p {
    font-size: 1.8rem;
}
.statistics-value span {
    font-size: 5.5rem;
}
/* news coponent */
.news {
    margin-top: 335px;
}
.news h2 {
    font-size: 5.5rem;
    text-align: center;
    margin-bottom: 100px;
}
.news .row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
.news-content {
    max-width: 570px;
    margin: 10px;
}
.news-square {
    height: 300px;
    background-color: #d9e8f7;
    border-radius: 5px;
}
.news-text {
    margin: 32px 0 40px 48px;
}
.news-text h3 {
    font-size: 2.1rem;
    line-height: 32px;
    color: #0b51ca;
}
.news-text p {
    font-size: 1.6rem;
    line-height: 28px;
    margin-top: 34px;
}
.news-content:hover h3 {
    font-weight: bold;
}
.news-content:hover {
    border-bottom: 1px solid #0b51ca;
}
/* review component */
.block-reviews {
    margin-top: 330px;
    margin-bottom: 368px;
}
.block-reviews h2 {
    margin-bottom: 100px;
}
.review-btn {
    margin: 0 10px;
    border-radius: 50%;
    border: none;
    width: 74px;
    height: 74px;
    box-shadow: 0 45px 62px rgba(51, 153, 255, 0.35);
    background-color: #ffffff;
}
.review-btn:hover {
    background-color: #0b51ca;
    color: #ffffff;
}
.review-text h3:first-child {
    margin-bottom: 87px;
}
.review-text h3:nth-child(2) {
    margin-bottom: 36px;
}

[alt="lines"] {
    z-index: -100;
}

@media (max-width: 1200px) {
    .find-form-image {
        right: 15px;
    }
    .statistics {
        display: none;
    }
    [alt="lines"] {
        display: none;
    }
    .explore-docas {
        margin-top: 150px;
    }
    .explore-docas-text {
        width: 100%;
        margin: 0;
    }
    .explore-docas-text h2 {
        text-align: center;
    }
    .explore-docas-btns {
        margin-left: 0;
        text-align: center;
    }
    .news-text {
        margin: 32px 0 0 0;
    }
    .news {
        margin-top: 150px;
    }
    .block-reviews {
        margin: 150px auto;
    }
}

@media (max-width: 992px) {
    .find-form-image {
        opacity: 0.3;
        width: 100%;
        right: 50%;
        -webkit-transform: translateX(50%);
                transform: translateX(50%);
    }
    .square {
        margin: 0;
    }
    .form-filter {
        padding: 0;
    }
}

@media (max-width: 768px) {
    .square {
        max-width: 100%;
    }
    .macbook {
        display: block;
    }
    .macbook img {
        width: 100%;
    }
    .explore-docas-btns button {
        height: 50px;
        width: 150px;
    }
    .news {
        display: none;
    }
    .block-reviews {
        display: none;
    }
    .block-last-form {
    margin-top: 0;
    }
}

@media (min-width: 992px) {
    .news .row {
        flex-wrap: nowrap;
    }
}

@media (min-width: 1400px) {
    .square-btns li {
        font-size: 2.8rem;
    }
}

