.logo-footer {
    width: 177px;
}
footer {
    height: 395px;
}
.p-footer {
    height: 40px;
    opacity: 0.7;
    color: #39436f;
    font-family: Niramit;
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 28px;
}
.footer-logo p {
    padding: 50px 0;
}

@media (max-width: 1200px) {
    footer {
        height: unset;
        margin-bottom: 50px;
    }
    .footer-logo {
        display: flex;
        justify-content: space-between;
        width: 100%;
    }
    .footer-logo p {
        padding: 0;
    }
    .docas24-footer-nav,
    .docas24-footer-nav ul {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
    }
    .docas24-footer-nav p {
        line-height: 30px;
        padding: 0 30px;
    }
}

@media (max-width: 768px) {
    footer {
        margin: 100px 0 50px 0;
    }
}

@media (max-width: 576px) {
    .footer-logo {
        justify-content: center;
        flex-wrap: wrap;
    }
    .footer-logo * {
        padding: 0 10px !important;
    }
}
@font-face {
  font-family: 'Niramit';
  font-style: normal;
  font-weight: 400;
  src: url(/static/media/Niramit-Regular.da1d2e2f.ttf) format('truetype');
}
html {
    font-size: 62.5%;
}
body {
    font-family: 'Niramit', sans-serif;
}
h1 {
    font-size: 5.8rem;
}
h2 {
    font-size: 4.8rem;
    letter-spacing: .8px;
    font-weight: bold;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.4rem;
}
h5 {
    font-size: 2rem
}
p {
    font-size: 1.6rem;
}
a {
    color: #000;
}
a:hover {
    text-decoration: none;
}
ul {
    list-style-type: none;
    padding: 0;
}
.selected {
    color: #0b51ca;
}
.selected-background {
    background: #0b51ca;
}
.btn-primary {
    background-color: #0b51ca;
    border-radius: 0;
}
.docas-btn-primary {
    color: #fff;
    height: 60px;
    width: 200px;
    box-shadow: 0 25px 42px rgba(69, 137, 254, 0.35);
    background-color: #0b51ca;
    border: none;
    font-size: 1.8rem;
    letter-spacing: 1.5px;
}
.docas-btn-outline-primary {
    color: #0b51ca;
    height: 60px;
    width: 200px;
    box-shadow: 0 25px 42px rgba(69, 137, 254, 0.35);
    background-color: #fff;
    border: 2px solid #0b51ca;
    font-size: 1.8rem;
    letter-spacing: 1.5px;
}
.docas-btn-outline-primary:hover {
    color: #fff;
    background-color: #0b51ca;
}
.docas-btn-transparent {
    color: #0b51ca;
    height: 50px;
    width: 150px;
    border: 2px solid #0b51ca;
    margin: 8px 0 0 135px;
    line-height: 45px;
    text-align: center;
}
.docas-input {
    height: 60px;
    border: 2px solid #0b51ca;
    font-size: 2.4rem;
    position: relative;
}

.fa-search {
    position: absolute;
    align-self: center;
    right: 10px;
}

.input-group-text {
    width: 55px;
    background: no-repeat;
    border: 2px solid #0b51ca;
    border-left: none;
}
.white {
    color: #ffffff;
}

.container-fluid {
    max-width: 1920px;
}

@media (max-width: 1600px) {
    html {
        font-size: 50% !important;
    }
